import {Component} from '@angular/core';
import {ROUTER_DIRECTIVES} from  '@angular/router';


@Component({
    selector: 'menu-bar',
    template: `
<nav>
  <div class="menubar-bg">
     <div class="dropdown">
         <button class="dropbtn"><dict [index]="'Sales'"></dict> </button>
         <div class="dropdown-content">


            <a [routerLink]="['NewItem']" id="linkNewItem"><dict [index]="'Add new item'"></dict></a>
            <a [routerLink]="['NewPartner']"><dict [index]="'Add new partner'"></dict></a>
             <a [routerLink]="['InvoiceList']"><dict [index]="'List of invoice'"></dict></a>
        </div>
    </div>
    <div class="dropdown">
         <button class="dropbtn"><dict [index]="'Setup'"></dict></button>
         <div class="dropdown-content">
            <a [routerLink]="['Login']"><dict [index]="'Login'"></dict></a>
            <a [routerLink]="['Admin']"><dict [index]="'Admin'"></dict></a>


        </div>
    </div>
   </div>
</nav>


    `,
    styleUrls: ["../src/css/menu-bar.css"],
    directives: [ROUTER_DIRECTIVES]
})
export class MenuBarComponent {
    showSale = false;

    onMouseOut(event) {
        var e = event.toElement || event.relatedTarget;
        if (e.parentNode == this || e == this) {
            return;
        }
        console.log(e.nodeType);
        // handle mouse eve
    }


}
